import * as React from 'react';
import {useState} from "react";
import {withNamespaces, WithNamespaces} from "react-i18next";
import {Button, Grid, Message, Modal} from "semantic-ui-react";

interface OwnProps {

}

type UpgradeProps = OwnProps & WithNamespaces;

const Upgrade = (props: UpgradeProps) => {

    const [showModal, setShowModal] = useState(false);

    const handleShowModal = () => setShowModal(true);

    const handleCloseModal = () => {
        setShowModal(false);
    };

    return <>
        <div className="middle aligned item">
            <div className="ui big header">Free Version<div className="ui blue basic horizontal label">Beta</div></div>
            <Grid className="stackable two column">
                <Grid.Column>
                    <Button className="positive" onClick={handleShowModal}>Upgrade</Button>
                </Grid.Column>
                <Grid.Column>
                    <div className="sub header">more fun, more features, more progress</div>
                </Grid.Column>
            </Grid>
        </div>
        <Modal open={showModal} closeIcon={true} onClose={handleCloseModal}>
            <Modal.Header>{ props.t("insp.subscribe.betaModal.header") }</Modal.Header>
            <Modal.Content>
                <Message icon="dollar" warning={true} content={props.t('insp.subscribe.betaModal.content')} />
            </Modal.Content>
        </Modal>
    </>;
};

export default withNamespaces()(Upgrade);
